import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const CustomersPage = React.lazy(() =>
  import(/* webpackChunkName: "customers" */ './customers')
);
const CustomerDetail = React.lazy(() =>
  import(/* webpackChunkName: "customer-detail" */ './customer-detail')
);

const PagesCustomers = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/all`} />
      <Route
        path={`${match.url}/all`}
        render={(props) => <CustomersPage heading='menu.customers-all' {...props} />}
      />
      <Route
        path={`${match.url}/my-customers`}
        render={(props) => <CustomersPage heading='menu.customers-my-customers' {...props} />}
      />
      <Route
        path={`${match.url}/:id`}
        render={(props) => <CustomerDetail {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default PagesCustomers;
